<template>
    <div class="manage">
        <el-dialog
            title="提示"
            :visible.sync="dialogVisible"
            :before-close="handleClose"
            width="50%"
            >
<!--          用户的表单信息-->
          <el-form ref="form" :rules="rules" :inline="true" :model="form" label-width="80px" >
              <el-form-item label="姓名" prop="name">
                  <el-input placeholder="请输入姓名" v-model="form.name"></el-input>
              </el-form-item>
              <el-form-item label="年龄" prop="age">
                <el-input placeholder="请输入年龄" v-model="form.age"></el-input>
              </el-form-item>

              <el-form-item label="性别" prop="sex">
                <el-select v-model="form.sex" placeholder="请选择">
                  <el-option label="男" value="1"></el-option>
                  <el-option label="女" value="0"></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="出生日期" prop="birth">
                <el-date-picker
                    v-model="form.birth"
                    type="date"
                    placeholder="选择日期">
                </el-date-picker>
              </el-form-item>

            <el-form-item label="地址" prop="addr">
              <el-input placeholder="请输入地址" v-model="form.addr"></el-input>
            </el-form-item>

          </el-form>
          <span slot="footer" class="dialog-footer">
          <el-button @click="cancel">取 消</el-button>
          <el-button type="primary" @click="submit">确 定</el-button>
          </span>
        </el-dialog>
        <div class="manage-header">
            <el-button @click="dialogVisible = true" type="primary">
                + 新增
            </el-button>
        </div>
        <el-table
            :data="tableData"
            style="width: 100%">
          <el-table-column
              prop="name"
              label="姓名">
          </el-table-column>
          <el-table-column
              prop="sex"
              label="性别">
                <template slot-scope="scope">
<!--                  由于是mock生成的随机数据所以这么写-->
                  <span style="margin-left: 10px">{{ scope.row.sex == 1 ? '男' : '女'}}</span>
                </template>
          </el-table-column>
          <el-table-column
              prop="age"
              label="年龄">
          </el-table-column>
          <el-table-column
              prop="birth"
              label="出生日期">
          </el-table-column>
          <el-table-column
              prop="addr"
              label="地址">
          </el-table-column>
          <el-table-column
              prop="addr"
              label="地址">
              <template slot-scope="scope">
                <el-button size="mini" @click="handleEdit(scope.row)">编辑</el-button>
                <el-button size="mini" type="danger" @click="handleDelete(scope.row)">删除</el-button>
              </template>
          </el-table-column>
        </el-table>
    </div>
</template>

<script>
import { getUser } from '@/api'
export default {
  data() {
    return {
      dialogVisible: false,
      form: {
        name: '',
        age: '',
        sex: '',
        birth: '',
        addr: ''
      },
      tableData:[
      ],
      rules: {
        name: [
          {required: true, message: '请输入姓名', trigger: 'blur'},    //防止忘记 trigger指触发条件 blue是失去焦点 change是改变数据
          {min: 2, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur'}
        ],
        age:[
          {required: true, message: '请输入姓名年龄', trigger: 'blur'}
        ],
        sex:[
          {required: true, message: '请选择性别', trigger: 'blur'}
        ],
        birth:[
          {required: true, message: '请选择出生日期', trigger: 'blur'}
        ],
        addr:[
          {required: true, message: '请输入地址', trigger: 'blur'}
        ],
      }
    }
  },
  methods:{
    //提交用户表单
    submit(){
      this.$refs.form.validate((valid) => {   //validate会传入是否校验成功的参数 valid为那个参数
          if(valid){
            //为true才对表单数据后续处理
            console.log(this.form,'form')
            //关闭弹窗
            // this.$refs.form.resetFields()
            this.dialogVisible = false
            // this.handleClose() 清空了取不到数据
          }
      })
    },
    //清空表单
    handleClose(){
        this.$refs.form.resetFields()
        this.dialogVisible=false
    },
    cancel(){
        this.handleClose()
    },
    handleEdit(){

    },
    handleDelete(){

    }
  },
  mounted(){
      getUser().then(({data}) => {
          console.log(data)
          this.tableData = data.list
      })
  }
}
</script>

<style scoped>

</style>